import React, { useRef, useState } from "react";
import {
	Grid,
	Container,
	makeStyles,
	Typography,
	Card,
	CardMedia,
	CardContent,
	Divider,
	Button,
	Link,
	CardActions,
} from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
	media: {
		height: 0,
		paddingTop: "75%", // 16:9
	},
	profile: {
		width: "100%",
		maxWidth: 200,
		display: "flex",
		margin: "auto",
	},
	actions: {
		display: "flex",
		justifyContent: "flex-end",
	},
}));
let images = [
	{ id: "biking", path: "images/biking.jpeg" },
	{ id: "skiing", path: "images/skiing.jpeg" },
	{ id: "standing", path: "images/standing.jpeg" },
	{ id: "sitting", path: "images/sitting.jpeg" },
];
const getRandom = () => Math.floor(Math.random() * images.length);

export default function Home() {
	const classes = useStyles();
	const [index, setIndex] = useState(getRandom());
	const setRandom = () => {
		let i = index;
		while (i === index) {
			i = getRandom();
		}
		setIndex(i);
	};
	return (
		<Container>
			<Grid container direction="row" spacing={2} justifyContent="center">
				<Grid item xs>
					<Card
						style={{ height: "100%", display: "flex", flexDirection: "column" }}
					>
						<CardContent>
							<Typography align="left" variant="h4">
								Hi.
							</Typography>
							<Typography align="left" variant="h4">
								I'm Nick.{" "}
							</Typography>
							<Typography align="left" variant="h4">
								I ride bikes.
							</Typography>
							<Typography align="left" variant="h4">
								I read books.
							</Typography>
							<Typography align="left" variant="h4">
								I write code.
							</Typography>
						</CardContent>
						<span style={{ flex: 1 }}></span>
						<CardActions className={classes.actions}>
							<Typography>
								Learn more about{" "}
								<a href="https://github.com/nmattise">what I've built</a> and{" "}
								<a href="https://www.strava.com/athletes/33281430">
									where I've ridden
								</a>
								.
							</Typography>
						</CardActions>
					</Card>
				</Grid>
				<Grid item>
					<img
						alt=""
						src={`${process.env.PUBLIC_URL}/${images[index].path}`}
						className={classes.profile}
						onClick={setRandom}
					/>
					<Card className={classes.profile}>
						<footer style={{ padding: "2px 12px 2px 12px" }}>
							<p
								style={{
									fontSize: "0.4rem",
									lineHeight: "normal",
								}}
							>
								I post these photos not because I am proud of my own visage, but
								because most people like to see the man with whom they are doing
								business.
							</p>
						</footer>
					</Card>
				</Grid>
			</Grid>
		</Container>
	);
}
